import { CST } from "../CST";

export class LoadScene extends Phaser.Scene {
    constructor() {
        super({
            key: CST.SCENCES.LOAD
        });
    }

    init() {
    }

    preload() {

        // Load Images, Spritesheets, Sound
        this.load.image("title_bg", "./assets/image/title_bg.jpg");
        this.load.image("options_button", "./assets/image/options_button.png");
        this.load.image("play_button", "./assets/image/play_button.png");
        this.load.image("logo", "./assets/image/logo.png");

        this.load.spritesheet("cat", "./assets/sprite/cat.png", {
            frameHeight: 32,
            frameWidth: 32
        });

        this.load.audio("title_music", "./assets/audio/shuinvy-childhood.mp3");

        // Create Loading Bar
        var loadingBar = this.add.graphics({
            fillStyle: {
                color: 0xffffff // White
            }
        });

        // Simulate Large Load
        /*
        for(let i=0; i < 100; i++) {
            this.load.spritesheet("cat"+i, "./dist/assets/sprite/cat.png", {
                frameHeight: 32,
                frameWidth: 32
            }); 
        }
        */
       
        /* Loader Events:
            complete - When Done Loading Everything
            progress - Loader Number Progress in Decimal
        */
       this.load.on("progress", (percent) => {
           loadingBar.fillRect(0, this.game.renderer.height / 2, this.game.renderer.width * percent, 50);
           console.log(percent);
       });
       this.load.on("complete", () => {
        // this.scene.start(CST.SCENCES.MENU, "Hello World");
       }); 
    }

    create() {
        this.scene.start(CST.SCENCES.MENU, "Hello World");
    }

    update() {
    }
}